<template>
  <div id="app">
    <navbar @reg="reg" @login="login" @myprofile="myprofile" @test="test" @sms="sms" @map="map" @google="google" @html5geo="html5geo" @friends="friends" @WS="WS"></navbar>
    <component4 v-if="state=='map'" @profile="profile" @myprofile="myprofile"></component4>
    <reg v-else-if="state=='reg'"></reg>
    <login v-else-if="state=='login'"></login>
    <profile v-else-if="state=='profile'" :prop="user" @map="map" @chat="chat"></profile>
    <myprofile v-else-if="state=='myprofile'"></myprofile>
    <sms v-else-if="state=='sms'" @profile="profile"></sms>
    <friends v-else-if="state=='friends'" @profile="profile"></friends>
    <WS v-else-if="state=='WS'" @profile="profile" @myprofile="myprofile"></WS>
    <chat v-else-if="state='chat'" @profile="profile"></chat>
  </div>
</template>

<script>
import component4 from './components/component4.vue'
import navbar from './components/navbar.vue'
import reg from './components/reg.vue'
import login from './components/login.vue'
import profile from './components/profile.vue'
import myprofile from './components/myprofile.vue'
import sms from './components/sms.vue'
import friends from './components/friends.vue'
import WS from './components/WS.vue'
import chat from './components/chat/App.vue'

export default {
  name: 'app',
  components:{component4, navbar, reg, login, profile, myprofile, sms, friends, WS, chat},
  data(){
    return{
      state:localStorage.getItem("state")!=undefined ? localStorage.getItem("state"): 'map',
      user:'',
      showModal:false,
    }
  },
  mounted(){
    if(localStorage.getItem("state")==null){localStorage.setItem('state','map')}
  },
  methods:{
    reg(){
      this.state='reg'
    },
    map(){
      this.state='map'
      localStorage.setItem("state", 'map')
    },
    login(){
      this.state='login'
    },
    profile(){
      this.state='profile'
    },
    myprofile(){
      this.state='myprofile'
    },
    sms(){
      this.state='sms'
    },
    chat(){
      this.state='chat'
    },
    friends(){
      this.state='friends'
    },
    WS(){
      this.state='WS'
      localStorage.setItem("state", 'WS')
    }
  },
 }
</script>

<style>
#app{
  height: auto;

}
</style>



