<script>
  import axios from 'axios'

  export default {
     data(){
        return{
          sessions:null,
          user:null,
          me:localStorage.getItem("me"),
        }
     },
    mounted(){
       this.get_sessions();
    },
    methods:{
      get_sessions(){
        axios.post(localStorage.getItem("s")+'/chat/get_sessions/', {me:this.me})
          .then((response) => {
           this.sessions=response.data;
      });
      },
      chat(user){
          localStorage.setItem("user", user);
          this.$emit('profile');
      },
      deleteSession(user){
          if(confirm('Сессия будет удалена у вас и вашего собеседника! Удалить?')){
            axios.post(localStorage.getItem("s")+'/chat/del_session/', {me:this.me, user:user})
            .then((response) => {
        });
          }
        }
    },
}
</script>

<template>
  <div>
    <div class="content-list">
      <mu-list>
        <mu-sub-header><h4>Сообщения ✉</h4></mu-sub-header>
        <mu-list-item :key="session.id" v-for="session in sessions" @click="chat(session.user)">
          <h4>{{session.user}}</h4>
          <mu-avatar :src="'https://roma7x7.fvds.ru'+session.img" slot="leftAvatar" />
          <mu-button slot="right" @click="deleteSession(session.user)" name="button">X</mu-button>
        </mu-list-item>
      </mu-list>
      <mu-divider/>
    </div>
  </div>
</template>

<style scoped="">
  .content-list{
    text-align: center;
  }
</style>




