<script>
  import axios from 'axios';
//  import chat from './chat/app'


  export default {
    components:{chat},
   data(){
    return{
      chek:null,
      me:localStorage.getItem("me"),
      nick:localStorage.getItem("user"),
      lat:'',
      lng:'',
      online:'',
      img:'',
      index:null,
    }
    },
    mounted(){
      this.initProfile();
    },
    methods:{
      getChat(){
        this.$emit('chat');
        localStorage.setItem('img',this.img)
      },
      initProfile(){
        axios.post(localStorage.getItem("s")+'/app/profile/', {user:this.nick, me:this.me})
          .then((response) => {
            this.lat=response.data.latitude;
            this.lng=response.data.longitude;
            this.img='https://roma7x7.fvds.ru'+response.data.img;
            this.chek=response.data.chek
      });
    },
      addFriend(){
        axios.post(localStorage.getItem("s")+'/app/friends/', {me:this.me, user:this.nick, add_friend:'OK'})
            .then((response) => {
              this.chek='yes'
        });
      },
      delFriend(){
        axios.post(localStorage.getItem("s")+'/app/friends/', {me:this.me, user:this.nick, del_friend:'OK'})
            .then((response) => {
              this.chek='no'
        });
      },
      getPos(){
        axios.post(localStorage.getItem("s")+'/app/getPos/', {user:this.nick})
            .then((response) => {
              let lat=response.data.latitude;
              let lng=response.data.longitude;
              localStorage.setItem('lat',lat)
              localStorage.setItem('lng',lng)
              localStorage.setItem('getpos', true)
              this.$emit('map');
            })
            .then((response)=>{
              localStorage.removeItem('getpos')
            });
      }
  },
    computed:{
     meen(){
      if (this.chek=='no'){return true}
      if (this.chek=='yes'){return false}
     },
    muun(){
      if (this.chek=='no'){return false}
      if (this.chek=='yes'){return true}
    }
    },
    beforeDestroy(){
      localStorage.removeItem("user");
    }
}
</script>

<template>
  <div class="allpage">
    <div class="container general">
        <div class="row">
            <div class="col-sm-12">
               <h1>{{nick}}</h1>
            </div>
        </div>
        <div class="row">
            <div class="col-sm-6">
              <img class="ava" :src="img"><br>
              <button class="btn btn-primary" @click="addFriend" v-if="meen">Добавить к избранным</button>
              <button class="btn btn-primary" @click="delFriend" v-if="muun">Удалить из избранных</button>
              <button class="btn" @click="getPos">Показать на карте</button>
              <button class="btn btn-info" @click="getChat">Написать</button>
            </div>
          <!--<div class="col-sm-6 block">-->
            <!--<chat></chat>-->
          <!--</div>-->
        </div>
    </div>
  </div>
</template>

<style scoped="">

  .row{
    display: block;
  }
  h1, h2 {
	text-align: center;
}
.button{
	margin-top: 10px;
}
.form-group1{
	padding-bottom: 15px;
	padding-top: 10px;
  margin-left: 8px;
}
.form-group2{
	margin-left: -40px;
  margin-top: 10px;
}
.allpage{
  background: #EDEEF0;
  text-align: center;
}
.general{
  width: auto;
  height: auto;
}
  .ava{
    height: 300px;
    width: auto;
  }
  .block{
    height:0;
    }

</style>





