<script>
import { mapGetters } from 'vuex'
import MyMsg from './myMsg'
import OtherMsg from './otherMsg'
//import axios from 'axios';
import MuAvatar from "../../../../node_modules/muse-ui/src/avatar/avatar.vue";

export default{
  components: {MyMsg, OtherMsg, MuAvatar},
  data () {
    return {
      me: localStorage.getItem("me"),
      user:localStorage.getItem("user"),
      room:null,
      chatMsg: '',
      container: {},
      img:localStorage.getItem("img"),
    }
  },
  created () {
    this.$store.dispatch('SetWebSocket', new WebSocket('wss://roma7x7.fvds.ru/chat/'))
    let list=[this.me,this.user].sort()
    this.room=list[0]+'-'+list[1]
  },
  beforeMount(){
    setTimeout(function(){
          this.getSocket.send(JSON.stringify({
            "command": "join",
            "room": this.room,
          }))
        }.bind(this),1000)
  },
  mounted () {
    this.container = document.querySelector('.chat-content')
    const that = this

    // Инициализировать новую информацию о разговоре
    this.$store.dispatch('SetChatMsg')

    //========================================================

    // Информация о диалоге истории загрузки
    const msgData = {
        'username': this.me,
        'other_user': this.user,
      };
    this.$store.dispatch('GetMessHistory', msgData)

//=================================================================

    // Автоматическая прокрутка вниз
    setTimeout(() => {
      this.$nextTick(() => {
        this.container.scrollTop = 10000
      })
    }, 1000);
    this.getSocket.onmessage = (message) => {
      let receivedMsg = JSON.parse(message.data);
      const now = new Date();
      const msgData = {
        'username': receivedMsg.username,
        'msg': receivedMsg.msg,
        'gentime': now.toISOString()
      };
      that.$store.dispatch('AddChatMsg', msgData)
    }
  },
  methods: {
    prof(){
      this.$emit('profile');
    },
    goback () {
      // Отключить при возврате
      this.$store.dispatch('SetWebSocket', null)
      this.$router.goBack()
    },
    send () {
      const now = new Date();
      const msgData = {
        "command": "send",
        'username': this.me,
        'other_user':this.user,
        'room': this.room,
        'msg': this.chatMsg,
        'gentime': now.toISOString()
      };
      this.$store.dispatch('AddMessHistory', msgData)
      this.getSocket.send(JSON.stringify(msgData))
      this.chatMsg = ''
    },
    scrollToBottom () {
      let container = document.querySelector('.chat-content')
      let scrollHeight = container.scrollHeight
      container.scrollTop = scrollHeight
    }
  },
  computed: {
    ...mapGetters([
      'getSocket',
      'getUsers',
      'getInfo',
      'getMsgHistoryInfo'
    ])
//    getSocket(){return this.$store.getters.getSocket},
//    getUsers(){return this.$store.getters.getUsers},
//    getInfo(){return this.$store.getters.getInfo},
//    getMsgHistoryInfo(){return this.$store.getters.getMsgHistoryInfo},
  },
  updated () {
    this.scrollToBottom()
  },
  beforeDestroy(){
	  this.getSocket.send(JSON.stringify({
        "command": "leave",
        "room": this.room,
    }));
  },
  destroyed(){
    this.getSocket.close()
  }
}
</script>

<template>
  <div class="container">
  <div class="row">
   <div class="chat-room">
     <mu-avatar :src="img" @click="prof" class="ava"/>
    <!-- Чат-чат -->
    <div class="chat-content">
      <div v-for="obj in getMsgHistoryInfo" :key="obj.id">
        <other-msg v-if="obj.username!=me" :name="obj.username" :msg="obj.msg" :mytime="obj.gentime"></other-msg>
        <my-msg v-if="obj.username==me" :name="obj.username" :msg="obj.msg" :mytime="obj.gentime"></my-msg>
      </div>
      <div v-for="obj in getInfo" :key="obj.id">
        <other-msg v-if="obj.username!=me" :name="obj.username" :msg="obj.msg" :mytime="obj.gentime"></other-msg>
        <my-msg v-if="obj.username==me" :name="obj.username" :msg="obj.msg" :mytime="obj.gentime"></my-msg>
         <!--:avatar="avatar"-->
      </div>
    </div>
    <!-- Область ввода -->
    <div class="bottom">
      <div class="chat-input">
        <div class="msg-input" @keyup.enter="send">
          <input type="text" v-model="chatMsg">
        </div>
      </div>
    </div>
  </div>
  </div>
  </div>
</template>

<style scoped>
  .ava:hover{
    cursor: hand;
  }
  .chat-room{
    width: 600px;
  }
.chat-content {
  width: 100%;
  height: 80%;
  background: white;
  overflow-x: hidden;
  overflow-y: visible;
}
.bottom {
  width: 100%;
  height: 50px;
  bottom: 0;
  left: 0;
  z-index: 1;
  background: #eeeff3;
}
.msg-input {
  background: #eeeff3;
  padding: 4px;
}
input {
  width: 100%;
  height: 42px;
  box-sizing: border-box;
  border: 1px solid #8c8c96;
  color: #333333;
  font-size: 18px;
  padding-left: 5px;
}
</style>



