import 'babel-polyfill';
import Vue from 'vue'
import App from './App.vue'
import store from './components/chat/store'
import MuseUI from 'muse-ui'
import 'muse-ui/dist/muse-ui.css'
// import 'muse-ui/dist/theme-carbon.css'

Vue.use(MuseUI);

new Vue({
  el: '#app',
  store:store,
  render: h => h(App)
});



