<template>
  <div>
    <div class="container general">
            <div class="row">
                <div class="col-sm-12 main">


                         <form id="myform" action="" method="post" class="form-inline text-center" @submit.prevent="post">

                         <div class="field">
                           <h1>Регистрация</h1>
                             <label>nick: </label>
                             <input class="form-control" v-model="nick">
                         </div>
                           <br>
                          <div class="field">
                             <label>password: </label>
                             <input class="form-control" v-model="pass">
                         </div>
                           <br>
                           <div class="field">
                             <label>confirm: </label>
                             <input class="form-control" v-model="confirm">
                           </div>
                             <br>
                           <button type="submit" class="btn btn-primary button">Отправить</button>

                       <div id="output2"></div>
                      </form>

                </div>
            </div>
        </div>
  </div>
</template>

<script>
  import axios from 'axios'

  export default {
  data(){
    return{
      nick:'',
      pass:'',
      confirm:'',
    }
  },
  methods:{
    post(){
      if (this.pass==this.confirm){
        if(this.pass.length<8){alert('Должно быть минимум 8 символов в пароле!')}
        else {
          axios.post(localStorage.getItem("s")+'/app/reg/', {username:this.nick, password:this.pass})
          .then((response) => {
             if (response.data!='error'){
                localStorage.setItem("me", response.data.me);
                localStorage.setItem("token", response.data.token);
                localStorage.setItem("mypos", 'true');
                document.location.reload(true);
             }
            else{
              alert('Такой ник уже есть, придумай другой!')
            }
        });
        }
      }
      else{
        alert('Пароли не совпадают!')
      }
    },
  }
}
</script>

<style>
  h1, h2 {
	text-align: center;
}
.button{
	margin-top: 10px;
}
  .field{
    text-align: center;
  }
   label {
   display: block;
  float: left;
  width: 70px;
  height: 15px;
  }
  .main{
    text-align: center;
    width: 230px;
    height: 329px;
  }
</style>



