<script>
  import axios from 'axios'
export default {
    components:{},
  data(){
    return{
      icon:localStorage.getItem('mypos')=='true'?'✔':'✘',
    }
  },
  mounted(){
    this.myServer();
  },
  methods:{
    myPos(){
      let x=localStorage.getItem("mypos");
       if (x=='false') {
         localStorage.setItem("mypos", true)
         this.icon='✔'
         document.location.reload(true);
       }
       if (x=='true') {
         localStorage.setItem("mypos", false)
         this.icon='✘'
       }
    },
    myServer(){
      let myserver = 'https://roma7x7.fvds.ru/api'
      localStorage.setItem("s", myserver)
    },
    logout(){
      axios.post(localStorage.getItem("s")+'/app/logout/', {token:localStorage.getItem("token")})
         .then(function(response){
           console.log(response);
           localStorage.removeItem("token");
           localStorage.removeItem("me");
           localStorage.removeItem("state");
           localStorage.removeItem("zoom");
           localStorage.removeItem("lat");
           localStorage.removeItem("lng");
           localStorage.removeItem("mypos");
           document.location.reload(true);
          })
    },
    map(){
      let state=localStorage.getItem("state")
      this.$emit(state)
    },
  },
  computed:{
    token(){
      return this.$store.state.token
    },
    me(){
      return localStorage.getItem("me")
    },
    //значение true когда пользователь авторизован
     meen(){
      if (localStorage.getItem("me")!=undefined){return true}
      if (localStorage.getItem("me")==undefined){return false}
    },
    //значение true когда пользователь не авторизован
    muun(){
      if (localStorage.getItem("me")!=undefined){return false}
      if (localStorage.getItem("me")==undefined){return true}
    }
  }
}
</script>
<template>
  <div>
    <nav class="navbar navbar-no-bg" role="navigation">
      <div class="container general">
        <a class="navbar-brand" href=""><img src="assets/logo/logo.png" alt="logo"></a>
        <div class="navbar-header navbar-right">
              <a class="scroll-link navlinks" v-if="meen" @click="map"><img src="/assets/icons/white/map.png"></a>
              <a class="scroll-link navlinks" v-if="meen" @click="$emit('sms')"><img src="/assets/icons/white/sms.png"></a>
              <a class="scroll-link navlinks" v-if="meen" @click="$emit('friends')"><img src="/assets/icons/white/friends.png"></a>
              <a class="scroll-link navlinks" v-if="meen" href=""><img src="/assets/icons/white/reload.png"></a>
              <a class="scroll-link navlinks" v-if="muun" @click="$emit('login')">Войти</a>
              <a class="dropdown a">
                <a data-toggle="dropdown" class="scroll-link navlinks dropdown-toggle" v-if="meen"><img src="/assets/icons/white/options.png"></a>
                <ul class="dropdown-menu">
                  <li><a class="select" @click="myPos()"><img src="/assets/icons/white/position_black.png">{{icon}}</a></li>
                  <li class="divider"></li>
                  <li><a class="select" v-if="meen" @click="$emit('myprofile')">{{me}}</a></li>
                  <li class="divider"></li>
                  <li><a class="select" @click="$emit('map')">map</a></li>
                  <li class="divider"></li>
                  <li><a class="select" @click="$emit('WS')">realtime map</a></li>
                  <li class="divider"></li>
                  <li><a class="select" v-if="meen" @click="$emit('reg')">Регистрация</a></li>
                  <li class="divider"></li>
                  <li><a class="select" v-if="meen" @click="logout()">Выйти</a></li>
                </ul>
              </a>
        </div>
      </div>
    </nav>
  </div>
</template>
<style scoped>
.a{
  margin-left: -25px;
}
@media (max-width: 768px) {
  .navbar-brand {
    display: none;
  }
}
.navbar{
  border: none;
  background: #2C608C;
  margin-bottom: 0;
}
.navbar-brand{
  padding: 0;
}
.general{
  margin: auto;
}
.navlinks{
  color: white;
}
.navlinks:hover{
  background: #4a76a8;
  cursor: hand;
}
.select:hover{
  cursor: hand;
}
.icon-bar{
  background: white;
}

a{
  padding: 22px;
}
.navbar-header{
  margin-top: 10px;
  margin-bottom: -10px;
}
</style>



