<script>
  import axios from 'axios'

  export default {
       data(){
          return{
            friends:null,
            user:null,
            me:localStorage.getItem("me"),
          }
       },
      mounted(){
         this.get_friends();
      },
      methods:{
        get_friends(){
          axios.post(localStorage.getItem("s")+'/app/friends/', {me:this.me, get_friends:'OK'})
            .then((response) => {
             this.friends=response.data;
        });
        },
        chat(user){
            localStorage.setItem("user", user);
            this.$emit('profile');
        }
      },
}
</script>

<template>
  <div>
    <div class="content-list">
      <mu-list>
        <mu-sub-header><h4>Избранные ☆</h4></mu-sub-header>
        <mu-list-item :key="friend.id" v-for="friend in friends" @click="chat(friend.user)">

          <h4>{{friend.user}}</h4>
          <mu-avatar :src="'https://roma7x7.fvds.ru'+friend.img" slot="leftAvatar" />
        </mu-list-item>
      </mu-list>
      <mu-divider/>
    </div>
  </div>
</template>

<style scoped="">
  .content-list{
    text-align: center;
  }
</style>



