<template>
<div>
  <div id="map"></div>
</div>
</template>

<script>
  import axios from 'axios'
export default {
	data(){
		return {
		  me:localStorage.getItem("me"),
      map:null,
      tileLayer:null,
      showModal:false,
      mypos: localStorage.getItem("mypos")!=undefined ? localStorage.getItem("mypos") : true,
      lat:localStorage.getItem("lat")!=undefined ? localStorage.getItem("lat") : 53.9045398,
      lng:localStorage.getItem("lng")!=undefined ? localStorage.getItem("lng") : 27.5615244,
      zoom:localStorage.getItem("zoom")!=undefined ? localStorage.getItem("zoom") : 10,

      socket:null,
      latpos:null,
      lngpos:null,
      img:null,
		}
	},
	mounted() {
	      this.initMap();
        setTimeout(function(){this.initLocation()}.bind(this),1000);
        this.initMoov();
        this.getMarkers();
  },
	methods: {
    initMoov(){
      this.map.on("moveend", this.getMoov);
    },
    getMoov(e){
      let zoom = this.map.getZoom();
      localStorage.setItem("zoom", zoom);

      let center = this.map.getCenter();
      localStorage.setItem("lat", center.lat);
      localStorage.setItem("lng", center.lng);
    },
    initMap() {
      this.socket = new WebSocket('wss://roma7x7.fvds.ru/app/')
      let latlng=[this.lat,this.lng]
      this.map = L.map ('map'). setView (latlng, localStorage.getItem("zoom"));//[53.9045398,27.5615244]
      this.tileLayer = L.tileLayer ('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png',{
              maxZoom: 19,
              attribution: '© <a href="http://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors',
        }
      );
      this.tileLayer.addTo(this.map);
      this.map.on('dblclick', this.initMagazine)
    },
    initLocation() {
      let x;
      if(localStorage.getItem('getpos')==undefined){
        if (this.mypos=='false'){x=false}else{x=true};
      }
      else{x=false}
      this.map.locate({setView: x, maxZoom: localStorage.getItem("zoom"), watch:true, enableHighAccuracy:true});//watch:true - fo real time, setView:true - see locate, maxZoom: this.zoom
      this.map.on('locationfound', this.onLocationFound);
    },

    onLocationFound(e){

      const msgData = {
        'lat': e.latlng.lat,
        'lng': e.latlng.lng,
        'me': this.me,
        'img': this.img,
      };
      this.socket.send(JSON.stringify(msgData))
    },

    getMarkers() {
      let t=this
      axios.get(localStorage.getItem('s')+'/app/getMarkers/')
          .then(function (response) {
         let leafletView = new PruneClusterForLeaflet();
         let markers = []
         for (let i = 0; i < response.data.length; i++) {
           let data = response.data[i];
           let marker = new PruneCluster.Marker(data.latitude, data.longitude);
           marker.data.name = data.user
           marker.data.icon = L.icon({
             iconUrl:'https://roma7x7.fvds.ru'+data.img,
             iconSize:[30,30],
           });
           leafletView.PrepareLeafletMarker = function(leafletMarker, data) {
                leafletMarker.setIcon(data.icon);
                leafletMarker.on('click', function(){
                    if (data.name!=t.me){
                       localStorage.setItem("user", data.name);
                       t.$emit('profile')
                    }
                    else {
                       t.$emit('myprofile')
                    }
                });
                leafletMarker.bindTooltip(data.name)
           };
           markers.push(marker)
           leafletView.RegisterMarker(marker);
         }
          t.socket.onmessage = function(event) {
            let msg = JSON.parse(event.data);
            for (let i = 0; i < markers.length; i++) {
              let m=markers[i]
              if (msg.me==m.data.name){
                m.position.lat = msg.lat
                m.position.lng = msg.lng
              }
            }
          }
     t.map.addLayer(leafletView);
    })
    },

    initMagazine(e){
      localStorage.setItem('magazineLat', e.latlng.lat)
      localStorage.setItem('magazineLng', e.latlng.lng)
	    this.$emit('magazine')
    }
	},
  beforeDestroy(){
	  this.socket.close()
  }
}
</script>

<style scoped="">
  @import url('../../assets/leaflet/prunecluster/LeafletStyleSheet.css');
	#map {
  /*height: 94.3%;*/
    height: calc(100% - 51px);
  /*height: 100%;*/
  width: 100vw;
}
  .icon{
    background: deepskyblue;
    border-radius: 50%;
  }
  div#size, a#delete {
	position: absolute;
	right: 1em;
	top: 1em;
	background: white;
	color: black;
	padding: 0.4em;
	border-radius: 4px;
	z-index: 500;
}
</style>



