import request from '../utils/request'

export function getHistoryMsg (data) {
  return request({
    url: '/chat/history_msg/',
    method: 'post',
    data
  })
}

export function addHistoryMsg (data) {
  return request({
    url: '/chat/create_msg/',
    method: 'post',
    data
  })
}



