<template>
  <div>
    <div class="container general">
      <div class="row">
          <div class="col-sm-12 main">
             <h1>Авторизация</h1>
             <form id="myform" action="" method="post" class="form-inline text-center" @submit.prevent="post">
               <div class="form-group1">
                 <label>nick: </label>
                 <input class="form-control" v-model="nick">
               </div>
               <br>
               <div class="form-group2">
                 <label>password: </label>
                 <input class="form-control" v-model="pass">
               </div>
               <br>
               <button type="submit" class="btn btn-primary btn-orange button">Отправить</button>
             </form>
          </div>
      </div>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'

  export default {
  data(){
    return{
      nick:'',
      pass:'',
    }
  },
  methods:{
    post(){
      axios.post(localStorage.getItem("s")+'/app/login/', {username:this.nick, password:this.pass})
        .then((response) => {
        if (response.data!='error') {
          localStorage.setItem("me", response.data.me);
          localStorage.setItem("token", response.data.token);
          localStorage.setItem("mypos", 'true');
          document.location.reload(true);
        }
        else{
          alert('Неверные данные!')
        }
});
    },
  }
}
</script>

<style>
  h1, h2 {
	text-align: center;
}
.button{
	margin-top: 10px;
}
  .field{
    text-align: center;
  }
   label {
   display: block;
  float: left;
  width: 70px;
  height: 15px;
  }
  .main{
    text-align: center;
    width: 230px;
    height: 329px;
  }
</style>




