<script>
  import axios from 'axios'
  import PictureInput from 'vue-picture-input'
  export default {
    components: {PictureInput},
       data(){
          return{
            me:localStorage.getItem("me"),
            lat:'',
            lng:'',
            online:'',
            img:'',
            selectedFile:null,
            image:'',
          }
       },
      mounted(){
        this.initProfile();
      },
      methods:{

        initProfile(){
          axios.post(localStorage.getItem("s")+'/app/myprofile/', {me:this.me})
            .then((response) => {
              this.nick=localStorage.getItem("me");
              this.lat=response.data[0].latitude;
              this.lng=response.data[0].longitude;
              this.img='https://roma7x7.fvds.ru'+response.data[0].img;
        });
      },

      onFileSelected(event){
        this.image=event.target.files[0];
      },

        onChange (image) {
      console.log('New picture selected!');
      if (image) {
        console.log('Picture loaded.');
        this.image = image
      } else {
        console.log('FileReader API not supported: use the <form>, Luke!')
      }
    },

      onUpload(){
        const fd=new FormData();
        fd.append('image',this.image);
        fd.append('me',localStorage.getItem("me"));
        axios.put(localStorage.getItem("s")+'/app/upload/', fd)
          .then((response) => {
            this.img='https://roma7x7.fvds.ru'+response.data.img;
      });
      },
      deleteUser(){
        let pas=prompt('Вы действительно хотите удалить юзера '+this.me+'?\n'+
          'Все переписки с вашим участием будут удалены!\nТакже вы будете удалены из избранных у других юзеров!\n' +
          'Для удаления введите пароль и жмите "OK"!');
        if(pas!=null){
          axios.post(localStorage.getItem("s")+'/app/deleteUser/', {me:this.me, token:localStorage.getItem("token"), pas:pas})
          .then((response) => {
             if(response.data=='OK'){
               localStorage.removeItem("token");
               localStorage.removeItem("me");
               localStorage.removeItem("state");
               localStorage.removeItem("zoom");
               localStorage.removeItem("lat");
               localStorage.removeItem("lng");
               localStorage.removeItem("mypos");
               document.location.reload(true);
             }
             else{alert('Неверный пароль!')}
          });
        }
      },
  }
}
</script>

<template>
  <div class="allpage">
    <div class="container general">
            <div class="row">
                  <div class="col-sm-4">
                    <img class="ava" :src="img">
                  </div>
                  <div class="col-sm-8 block">
                    <h1>{{nick}}</h1>
                    <h2>latitude: {{lat}}</h2>
                    <h2>longitude: {{lng}}</h2>

                    <button class="btn btn-primary"><input type="file" @change="onFileSelected"></button>
                    <!--@ change - прослушиватель событий-->
                    <button class="btn btn-primary send" @click="onUpload">Upload</button>
                    <br><br>
                    <button class="btn btn-warning" @click="deleteUser">Delete user</button>
                  </div>
            </div>
        </div>
  </div>
</template>

<style scoped="">
  .row{
    display: block;
  }
  h1, h2 {
	text-align: center;
}
.button{
	margin-top: 10px;
}
.general{
	/*margin-top: 300px;*/
}
.form-group1{
	padding-bottom: 15px;
	padding-top: 10px;
  margin-left: 8px;
}
.form-group2{
	margin-left: -40px;
  margin-top: 10px;
}
  /*vk style*/
.allpage{
  background: #EDEEF0;
  text-align: center;
}
.general{
  width: auto;
  height: auto;
}
  .ava{
    height: 300px;
    width: auto;
  }
  .send{
    /*position: absolute;*/
    /*bottom: 0px;*/
  }
  .block{
    height: 300px;
    }
</style>



